import { _decorator, Component, Node } from 'cc';
import { tab } from '../../../../Table/table_gen';
import { BattlePassView } from './BattlePassView';
const { ccclass, property } = _decorator;

@ccclass('BattlePassToggleItem')
export class BattlePassToggleItem extends Component {
    private _mainView:BattlePassView = null;
    private _passTab:tab.BattlePassTable = null;
    initData(table:tab.BattlePassTable,view:BattlePassView){
        this._passTab = table;
        this._mainView = view;
    }
    checkToggleItem(){
        this._mainView.switchView(this._passTab.BattlePassTab);
    }
}


